      ])
    },
    createChain () {
      let needApprove = this.approval == 'on'
      let orgName = this.orgName
      let data = {
        orgName: orgName,
        needApprove
      }
      if (!orgName) {
        this.$Message.error('')
        return
      }
      this.$http.post('/fbs/man/pbscn.do', data).then(res => {
        res = res.data
        if (res.retCode == '1') {
          this.next()
        }
      })
    }

  },
  created: function () {
