<template>
  <div class="chain-league">
    <div class="chain-league-in">
      <h3></h3>
      <h4></h4>
      <div>
        <div style="margin-bottom: 20px;">
          <span></span>
          <Tooltip
            placement="top"
            max-width="600"
            transfer
            content=':* * 1/31/3* 2/32/3*  '>
            <Icon type="ios-information-circle-outline" />
          </Tooltip>
        </div>
        <div>
          <RadioGroup class="approval" v-model="acceptLimit">
            <Radio label="0"></Radio>
            <Radio label="1/3">1/3</Radio>
            <Radio label="2/3">2/3</Radio>
            <Radio label="3/3"></Radio>
          </RadioGroup>
        </div>
      </div>
      <div>
        <div class="league-mem">
          <span></span>
          <button class="fr" @click="addModal = true"><Icon type="md-add-circle" /></button>
        </div>
        <Table :columns="columns1" :data="data1"></Table>
      </div>
      <div class="next-step">
        <Button size="large" long type="primary" @click="next"></Button>
      </div>
    </div>
    <Modal
      v-model="addModal"
      title=""
      @on-ok="ok"
      @on-cancel="cancel">
      <div class="add-modal-body">
        <div><Input placeholder="" v-model="name" /></div>
        <div><Input placeholder="" v-model="address" /></div>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  data () {
    let that = this
    let columns1 = [
      {
        title: '',
        key: 'name'
      },
      {
        title: '',
        key: 'address'
      },
      {
        title: '',
        render (h, p) {
          return h('a', {
            on: {
              click () {
                let index = p.index
                that.data1.splice(index, 1)
              }
            }
          }, '')
        }
      }
    ]
    let data1 = [

    ]
    return {
      acceptLimit: '0',
      columns1,
      data1,
      addModal: false,
      name: '',
      address: ''
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {

    },
    next () {
      this.confirm()
      // this.$emit('next', 'step4.3')
    },
    confirm () {
      let acceptLimit = this.acceptLimit
      let data = this.data1
      let name = []
      let address = []
      if (data.length) {
        data.forEach((item, index) => {
          name.push(item.name)
          address.push(item.address)
        })
      }
      let param = {
        name, address, acceptLimit
      }
      this.$http.post('/fbs/man/pbsci.do', param).then(res => {
        res = res.data
        if (res.retCode == '1') {
          this.$emit('next', 'step4.3')
        }
      }).catch(err => {
        // this.$Message.error('')
      }).then(() => {

      })
    },
    ok () {
      let name = this.name.trim()
      let address = this.address.trim()
      if (!name) {
        this.$Message.error('')
        return
      }
      if (!address) {
        this.$Message.error('')
        return
      }
      let data = {
        name,
        address
      }
      this.data1.push(data)
      this.name = ''
      this.address = ''
    },
    cancel () {
      this.name = ''
      this.address = ''
    }
  }
}
</script>

<style lang="less">
  .chain-league-in {
    h3 {
      font-size: 24px;
    }
    h4 {
      color: #16CEF1;
      padding: 18px 0;
    }
  }
  .league-mem {
    padding: 30px 0;
    .fr {
      color: #6094FF;
      background: #fff;
      border: 1px solid #6094FF;
      padding: 0 8px;
      border-radius: 12px;
      box-shadow: none;
      outline: none;
      cursor: pointer;
    }
  }
  .next-step {
    width: 500px;
    margin: 30px 0;
  }
  .add-modal-body {
    & > div:nth-child(1) {
      margin-bottom: 20px;
    }
  }
</style>
