<template>
  <div class="chain-configure">
    <div class="configure-in">
      <h3></h3>
      <h4></h4>
      <div class="configure-body">
        <div>
          <span></span>
          <RadioGroup class="encryption" v-model="client">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </div>
        <div class="encryption-note">* </div>
        <div>
          <span></span>
          <RadioGroup class="encryption" v-model="node">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </div>
        <div class="encryption-note">* </div>
        <div class="next-step">
          <Button long type="primary" size="large" @click="next"></Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      client: '1',
      node: '1'
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {

    },
    next () {
      let client = this.client
      let node = this.node
      // 
      let key1 = 'org.brewchain.client.transfer.encryption'
      // 
      let key2 = 'org.brewchain.node.transfer.encryption'
      let keys = [key1, key2]
      let values = [client, node]
      let data = {
        keys, values
      }
      this.$http.post('/fbs/man/pbsgo.do', data).then(res => {
        res = res.data
        if (res.retCode == 1) {
          this.$Message.success('')
          this.$emit('next', 'step4.5')
        } else if (res.msg) {
          this.$Message.error(res.msg)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .configure-in {
    h3 {
      font-size: 24px;
    }
    h4 {
      color: #16CEF1;
      padding: 18px 0;
    }
    .next-step {
      width: 60%;
      margin: 30px 0;
    }
  }
  .encryption-note {
    background:rgba(23,190,222,0.04);
    border-radius:4px;
    border:1px dashed rgba(23,190,222,1);
    color: #B1B0B0;
