<script>
export default {
  data () {
    return {
      step: '1',
      serverName: '',
      pubKey: '', // 
      entPubkey: '', // 
      orgName: '', // 
      existsUrl: ''// 
    }
  },
  watch: {
    step (n, o) {
      console.log(n, o)
      let that = this
      if (n == 2) {
        setTimeout(() => {
          that.step = '3'
        }, 3000)
      }
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    apply () {
      let orgName = this.orgName
      let existsUrl = this.existsUrl
      if (!existsUrl) {
        this.$Message.error('')
        return
      }
      if (!orgName) {
        this.$Message.error('')
        return
      }
      let data = {
        orgName, existsUrl
      }
      this.$http.post('/fbs/man/pbspo.do', data).then(res => {
        res = res.data
        if (res.retCode == 1) {
          this.step = 2
        }
      }).catch(err => {
        console.log(err)
      }).then(() => {

      })
    },
    getServerInfo () {
      // 
      // key = org.brewchain.account.coinbase.pubkey
      // 
      // key = org.brewchain.account.org.pubkey
      // POST /fbs/man/pbgpo.do
      this.getEntbkey()
      this.initPubkey()
    },
    init () {
      this.initServerName()
    },
    initServerName () {
      let serverName = this.$cookie.get('serverName')
      this.serverName = serverName
    },
    getEntbkey () {
      let data = {
        key: 'org.brewchain.account.org.pubkey'
      }
      this.$http.post('/fbs/man/pbgpo.do', data).then(res => {
        res = res.data
        if (res.retCode == '1') {
          this.entPubkey = res.value
        }
      }).catch(err => {
        console.log(err)
      }).then(() => [
        console.log('complete')
      ])
    },
    initPubkey () {
      let pubKey = cwv.genPubkey()
      let data = {
        seed: '',
        pubKey
      }
      this.$http.post('/fbs/man/pbgnc.do', data).then(res => {
        res = res.data
        this.pubKey = res.pubKey || ''
      }).catch(err => {
        console.log(err)
      }).then(() => {

      })
    },
    create () {
      let needApprove = this.approval == 'on'
      let orgName = this.orgName
      let data = {
        orgName: '',
        needApprove
      }
      if (!orgName) {
        this.$Message.error('')
        return
      }
      this.$http.post('/fbs/man/pbscn.do', data).then(res => {
        res = res.data
        if (res.retCode == '1') {
          this.next()
        }
      })
    }

  }
}
</script>

